.karta-container {
    background-color: rgb(64,187,192) ;

}

.karta-box {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    padding: 3em 0em;
}

.karta-left {
    max-width: 30em;
    background-color: rgb(68, 181, 185);
    padding: 2em 2em;
    border: 1px solid rgb(89, 89, 89);
    font-weight: 500;
    border-radius: 5px;
    margin-left: 2em;
    margin-right: 2em;
    box-shadow: 0 0 15px black;
    padding-bottom: 4em;
    
} 

.karta-left:hover {
    background-color: rgba(196, 196, 196, 0.155);
}

.karta-left li {
    margin-bottom: 1em;
}

.karta-right img {
    height: 10em;
    max-width: 20em;
    margin-left: 1em;
    border: 2px solid rgb(71, 71, 71);
    box-shadow: 0 0 15px rgba(65, 65, 65, 0.452);
   
}

.karta-images {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    
}

.karta-right {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin-left: 2em;
    margin-right: 2em;
}

.karta-right ul {
    font-weight: 600;
    margin-top: 1em;
  /*   font-family: 'Mukta Malar', sans-serif; */
   /*  font-family: 'Karma', serif; */
}



.one-info {
    display: flex;
    flex-direction: row;
    margin-top: 1em;
   
}
.one-info p {
    margin-left: 1.5em;
    /* font-family: 'Mukta Malar', sans-serif; */
   /*  font-family: 'Karma', serif; */
    line-height: 1.3em;
    font-weight: 600;
}

@media screen and (max-width:1370px) {
    .karta-left {
        max-width: 19em;
        max-height: 13em;
    }
}

@media screen and (max-width:1195px) {
    .karta-left {
        max-width: 30em;
        max-height: 6em;
        margin-bottom: 2em;
    }

    .karta-images img {
        margin-left: 1em;
        margin-right: 1em;
    }

    .karta-right ul {
        margin-top: 0;
    }
    .karta-right li:last-child {
        margin-top: 0.5em;
    }
}

@media screen and (max-width:652px) {
    .karta-left {
        max-width: 30em;
        max-height: 10em;
        margin-bottom: 2em;
    }
    .karta-images img {
        margin-bottom: 2em;
        /* width: 20em; */
    }
}

@media screen and (max-width:400px) {
    .karta-left {
       
        max-height: 13em;
       
    }
}